.presets{
    display: flex;
    justify-content: center;
    align-items:center;
    width: 100%;
    color: white;
}
.presets__holder{
    margin-top: 50px;
    max-width: 1300px;
    
    width: 100%;
    padding: 10px;
}
.presets__holder-top h2{
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    margin-bottom: 10px;
    
}
.presets__holder-top{
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
    align-items: center;
    font-size: 15px;
    font-weight: 500;
    width: 100%;
}
.presets__holder-top p{
    color: grey;
    font-size: 14px;
    font-weight: 600;
    margin: 0;
    width: 100%;
}

.presets__holder-top span{
    background-color:rgb(11, 11, 11);
    border: 1px grey solid;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: grey;
    font-size: 11px;
   
}
.presets__holder-top-arrow{
    margin-left: auto;
    margin-right: 10px;
}
.presets-holder-icon{
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: rgb(28, 28, 28, 0.5);
    width: 170px;
    margin-top: 5px;
    
    padding: 15px;
    border-radius: 10px;
    gap: 10px;

    
}
.presets-holder-icon img{
    border-radius: 5px;
    width: 100%;
    height: 170px;
    object-fit: cover;
    padding: 0;
    margin: 0;
}.presets-holder-icon h5{
    padding: 0;
    margin: 0;
    margin-right: auto;
    font-size: 14px;
}
.presets-holder-icon p{
    padding: 0;
    margin: 0;
    margin-right: auto;
    color: grey;
    font-size: 13px;
    font-weight: 500;
}


.presets__holder-itemsholder{
    margin-top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}