.whatsnew__sec{
    display: flex;
    justify-content: center;
    align-items:center;
    width: 100%;
    color: white;
}
.whatsnew{
    margin-top: 50px;
    max-width: 1300px;
    
    width: 100%;
    padding: 10px;
}
.whatsnew h2{
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    margin-bottom: 20px;
}
.whatsnew__genre{
    display: flex;
    justify-content: left;
    
    align-items: center;
    font-size: 15px;
    font-weight: 500;
    width: 100%;
}
.whatsnew__genre p{
    color: grey;
    font-size: 14px;
    font-weight: 600;
    border-bottom: 1px grey solid;
    margin: 0;
    padding-bottom: 15px;
    padding-right: 20px;
}
.whatsnew__genre p:nth-child(8){
    padding-right: 0;
}
.whatsnew__genre span{
    background-color: rgb(11, 11, 11);
    border: 1px grey solid;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: grey;
    font-size: 11px;
    
}
.whatsnew__genre-arrow{
    margin-left: auto;
    margin-right: 10px;
}
.whatsnew__items-icons{
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: rgb(28, 28, 28, 0.5);
    width: 170px;
    margin-top: 20px;
    padding: 15px;
    border-radius: 10px;
    gap: 10px;
}
.whatsnew__items-icons img{
    border-radius: 5px;
    width: 100%;
    height: 170px;
    object-fit: cover;
    padding: 0;
    margin: 0;
}.whatsnew__items-icons h5{
    padding: 0;
    margin: 0;
    margin-right: auto;
    font-size: 14px;
}
.whatsnew__items-icons p{
    padding: 0;
    margin: 0;
    margin-right: auto;
    color: grey;
    font-size: 13px;
    font-weight: 500;
}
.whatsnew__items{
    display: flex;
    justify-content: space-between;
    align-items: center;
   
}


