.topsample{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}
.top{
    width: 100%;
    max-width: 1300px;
    color: white;
}
.top__items-holder{
display: flex;
flex-wrap: wrap;
gap: 15px;

    
}
.top__items-holder-item{
flex: 1 0 21%;
display: flex;
background-color: rgb(28, 27, 27);
border-radius: 10px;
overflow: hidden;
justify-content: flex-start;
padding: 10px;
align-items: center;



}
.top__items-holder-item-detail{
    margin-left: 10px;
}
.top__items-holder-item p{
  margin: 0;
  font-size: 12px;
  color: grey;
    
}
.top__items-holder-item h5{
    margin: 0;
    font-size: 12px;
    font-weight: 500;
    
    

}

.top__items-holder-item img{
 object-fit: cover;
 width: 50px;
 height: 50px;
 border-radius: 2px;

}
