.header{
    width: 100%;
    display: grid;
    place-items: center;
    
   
}
.carousel{

    height: 400px;
    width: 100%;
    max-width: 1300px;
    margin-top: 30px;
    border-radius: 25px;
    position: relative;
    display: flex;
    justify-content: start;
   flex: 1;
    
    overflow: hidden;
   
}
.carousel__item{
    
    transition: transform 0.5s ease;
    width: 100%;
  
    white-space: nowrap;


   

}
.carousel h1{
    color: white;
     
    font-size: 2.5em;
    font-weight: 500;
    margin-bottom: 0;
    
    
}
.carousel__info{
    margin-left: 40px;
}
.carousel__detail{
display: flex;
align-items:center;
justify-content: flex-start;
gap: 10px;
margin-top: -12px;
}
.carousel__detail img{
    width: 30px;
    height: auto;
    border-radius: 50%;
}
.carousel__detail-text{
    color: rgb(154, 153, 153);
    font-size: 13px;
    font-weight: 500;
    margin-top: 0;
    margin-bottom: 20px;
    width: 40%;
    height: 80px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.carousel__detail h5{
    color: white;
    
     
    font-size: 0.9em;
    font-weight: 600;
}
.carousel__detail-top{
    background-color: rgb(229, 172, 17);
    font: 400 14px black "Poppins", sans-serif;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 20px;
    width: 65px;
    height: 20px;
    
    
}
.carousel__detail-top span{
    scale: 70%;
    margin-top: 3px;
}
.carousel__detail-top p{
    font-size:  8px;
    font-weight: 700;
    margin-top: 12px;
    
}
.carousel:nth-child(3){
    font: 500 14px grey "Poppins", sans-serif;
    width: 40%;
}
.carousel__buttons{
    display: flex;
    justify-content: left;
    width: 40%;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 15px;

    
}
.carousel__buttons a{
    background-color: rgb(82, 50, 199);
    border-radius: 55px;
    padding: 10px 15px;
    
    font-style: normal;
    text-decoration: none;
    color: white;
     
    font-weight: 600;
    font-size: 14px;
    margin-right: 50px;
}
.carousel__buttons span{
    background-color: rgb(56, 56, 56);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    color: white;
    width: 40px;
    height: 40px;
    margin-right: 15px;

}
.carousel__buttons h4{
    color: white;
   
    
    font-weight: 600;
    font-size: 14px;

}
.carousel__change-carousel{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    background-color: none;
    color: rgb(178, 178, 178);
    width: 70px;

}
.carousel__change-carousel span{
    color: white;
}

.carousel__back-image{
    height: 100%;
    width: 100%;
    position: absolute;
    background-size:contain;
    background-repeat: no-repeat;
    background-position: right center;
    
    z-index: -1;

}
.carousel__back-image::after{
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background: linear-gradient(to right, rgba(0,0,0,1),rgba(0,0,0,1), rgba(0,0,0,1), rgba(0,0,0,0.4), rgba(0,0,0,0));
    
}

