.spotlight{
    display: flex;
    justify-content: center;
    align-items:center;
    width: 100%;
    color: white;
    flex-wrap: wrap;
    margin-top: 60px;
    flex-direction: row;
    
    
   
}
.spotlight__info {
    justify-content: center;
    align-items: center;
    gap: 20px;
    display: flex;
    width: 40%;
    margin-right: 20px;
}

    
.spotlight__info-logo{
    height: 100%;
    width: 100%; 
    border-radius: 50%;
    border: 1px rgb(49, 49, 49) solid;   
}
.spotlight__info-details{
    display: flex;
    align-items: flex-start;
    justify-content: center;
   
    flex-direction: column;
}
.spotlight__info-details h2{
    margin: 0;
    padding: 0;
    
  
}
.spotlight__info-details p{
    margin: 0;
    padding: 0;
    font-size: 12.5px;
    font-weight: 500;
    line-height: 1.8;
    color: rgb(173, 173, 173);
    

}
.spotlight__info-detils-info{
    display: flex;
    align-items: center;
    justify-content: left;
    gap: 10px;
}
.spotlight__info-detils-info p{
    font-size: 11px;
    color: grey;
    margin: 0;
    padding: 0;
    
}

.spotlight__holder{
    width: 50%;
    padding: 10px;
    margin-top: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;
}
.spotlight__title h2{
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    margin-bottom: 10px;
   
    
}
.spotlight__title{
    display: flex;
    align-items: center;
    max-width: 1300px;
    font-size: 15px;
    font-weight: 500;
    width: 100%;
}
.spotlight__title p{
    color: grey;
    font-size: 14px;
    font-weight: 600;
    margin: 0;
    width: 100%;
}

.spotlight__title span{
    background-color:rgb(11, 11, 11);
    border: 1px grey solid;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: grey;
    font-size: 11px;
   
}
.spotlight__title-arrow{
    margin-left: auto;
    margin-right: 10px;
}
.spotlight-holder-icon{
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: rgb(28, 28, 28, 0.5);
    width: 170px;
    margin-top: 20px;
    padding: 15px;
    border-radius: 10px;
    gap: 10px; 
}
.spotlight-holder-icon img{
    border-radius: 5px;
    width: 100%;
    height: 170px;
    object-fit: cover;
    padding: 0;
    margin: 0;
}.spotlight-holder-icon h5{
    padding: 0;
    margin: 0;
    margin-right: auto;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
}
.spotlight-holder-icon p{
    padding: 0;
    margin: 0;
    margin-right: auto;
    color: grey;
    font-size: 13px;
    font-weight: 500;
}


