.navbar__menu{
    background-color: black;
    display: grid;
    place-items:center;
}
.navbar{
    width: 100%;
    height: 65px;
    background-color: none;
    max-width: 1300px;
   
    display: flex;
    color: white;
    align-items: center;
    justify-content: space-between;
    
}

.navbar__searchbar{
    margin: 15px;
    display: flex;
    justify-content: space-between;
    border: 1px grey solid;
    padding: 6px 12px;
    border-radius: 80px;
    
}
.navbar__searchbar input{
    background-color: transparent;
    background: transparent;
    color: rgb(76, 74, 74);
    border: 0;
    font-size: 14px;
    font-weight: 500;  
    font-style: oblique;
}
.navbar__searchbar i{
    -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  color: rgb(155, 154, 154);

  
}


.navbar__items{
    display: flex;
    width: 70%;
    align-items: center;
    gap: 20px;
    padding: 0 20px;
    font-family: 'Poppins', sans-serif;

}
.navbar__items h5{
    font-size: 15px;
    font-weight: 600;
    color: grey;
    margin: 0 10px;
}
.navbar__items h5:nth-child(3){
    margin-left: auto;
    
}
img {
    
    width: 100px;
    height: auto;
}
.navbar__items :nth-child(4){
    background-color: rgb(54, 54, 54);
    color: white;
    font-size: 13px;
    font-weight: 400;
    border-radius: 30px;
    border: 0;
    width: 80px;
    height: 30px;
    font-family: 'Poppins', sans-serif;
}
.navbar__items :nth-child(5){
    font-family: 'Poppins', sans-serif;
    background-color: rgb(80, 33, 220);
    color: white;
    font-size: 13px;
    font-weight: 400;
    border-radius: 30px;
    border: 0;
    width: 130px;
    height: 30px;
    position: relative;
    text-align: left;
    padding-left: 20px;
}
.navbar__items-started span{
 
 font-size: 13px;
 padding-left: 5px;   
 height: 100%;
margin-top: auto;
position: absolute;
top: 8px;
}