body{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    background-color: rgb(21, 21, 21);
}
#root{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
   
}
.App{
  width: 100%;
 
    
}
.whatsnew__items-icons:hover,
.trending-holder-icon:hover,
.spotlight-holder-icon:hover,
.top__items-holder-item:hover,
.fresh__items-holder-item:hover,
.presets-holder-icon:hover{
  background-color: rgba(51, 51, 51, 0.574);
  cursor: pointer;
}
.presets__holder-top span:hover,
.whatsnew__genre span:hover,
.trending__holder-top span:hover,
.spotlight__title span:hover,
.selections__holder-top  span:hover{
  background-color: rgba(0, 0, 0, 0.5);
  color: rgb(255, 255, 255);
  cursor: pointer;
  border: 1px rgb(25, 25, 25) solid;
}
