.promotion_holder-apply span{
 
    font-size: 13px;
    padding-left: 10px;   
    height: 100%;
   margin-top: auto;
   position: absolute;
   top: 15px;
   }
.promotion_holder-apply{
    font-family: 'Poppins', sans-serif;
    background-color: rgb(80, 33, 220);
    color: white;
    font-size: 13px;
    font-weight: 400;
    border-radius: 30px;
    border: 0;
    width: 130px;
    height: 45px;
    position: relative;
    text-align: left;
    padding-left: 20px;
    margin-top: 20px;
   }
   .promotion{
    width: 100%;
    background: linear-gradient(to top, rgb(7, 4, 31) , rgb(0, 0, 0));
    height: 500px;
    margin-top: 100px;

   }
   .promotion__holder{
    width: 100%;
    max-width: 1300px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    height: 100%;
    

   }
   .promotion__holder h1{
    font-size: 30px;
    font-weight: 500;
    text-align: center;
    width: 58%;
   }
   .promotion__holder-imageholder{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
   }
   .promotion__holder-imageholder img{
    border: 5px black solid;
    border-radius: 50%;
    object-fit: cover;
   
   }
   .promotion__holder-imageholder img:nth-child(1){
    width: 80px;
    height: 80px;
    z-index: 1;
    margin-right: -40px;
   
   }
   .promotion__holder-imageholder img:nth-child(2){
    width: 100px;
    height: 100px;
     margin-right: -40px;
    z-index: 2;
   }
   .promotion__holder-imageholder img:nth-child(3){
    width: 120px;
    height: 120px;
    z-index: 3;
   
   }
   .promotion__holder-imageholder img:nth-child(4){
    width: 100px;
    height: 100px;
    margin-left: -40px;
    z-index: 2;
    

   }
   .promotion__holder-imageholder img:nth-child(5){
    width: 80px;
    height: 80px;
    margin-left: -40px;
    z-index: 1;
   }