.selections{
    display: flex;
    justify-content: center;
    align-items:center;
    width: 100%;
    color: white;
}
.selections__holder{
    margin-top: 50px;
    max-width: 1300px;
    
    width: 100%;
    padding: 10px;
}
.selections__holder-top h2{
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    margin-bottom: 10px;
    
}
.selections__holder-top{
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
    align-items: center;
    font-size: 15px;
    font-weight: 500;
    width: 100%;
}
.selections__holder-top p{
    color: grey;
    font-size: 14px;
    font-weight: 600;
    margin: 0;
    width: 100%;
}

.selections__holder-top span{
    background-color: rgb(11, 11, 11);
    border: 1px grey solid;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: grey;
    font-size: 11px;
   
}

.selections__holder-top-arrow{
    margin-left: auto;
    margin-right: 10px;
}
.selections__holder-icon{
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: column;
    justify-content: flex-end;
    background-color: none;
    width: 170px;
    margin-top: 20px;
    padding: 15px;
    border-radius: 5px;
   
    height: 100px;
    overflow: hidden;
}
.selections__holder-icon::after{
    content: "";
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, rgba(0,0,0,1), rgba(0,0,0, 0.9), rgba(0,0,0,0.5), rgba(0,0,0,0.3), rgba(0,0,0,0), rgba(0,0,0,0));
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    
}
.selections__holder-icon img{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    padding: 0;
    margin: 0;
    z-index: -2;
    
    
    
}.selections__holder-icon h5{
    padding: 0;
    margin: 0;
    font-size: 16px;
}
.selections__holder-icon p{
    padding: 0;
    margin: 0;
    color: grey;
    font-size: 13px;
    font-weight: 500;
}

.selection__holder-itemsholder{
    margin-top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


